/* Responsive design */
@media (max-width: 768px) {
    .video{
        width: 100%;
    }
}

/* Television-like design */
.tv-border {
    position: relative;
    width: 100%;
    padding-top: 75%; /* 4:3 Aspect Ratio */
    background:rgba(252, 248, 248, 0.773);
    border-radius: 20%;
    overflow: hidden;
    box-shadow: 0 0 20px rgba(0,0,0,0.5);
}

.tv-screen {
    position: absolute;
    top: 10%;
    left: 10%;
    width: 80%;
    height: 80%;
}
.drop-button {
    display: inline-block;
    padding: 10px 20px;
    background-color: #f2f8f3;
    color: #2b2b2b;
    text-align: center;
    text-decoration: none;
    font-size: 16px;
    border: none;
    border-radius: 50px / 30px;
    transition: background-color 0.3s;
}

.drop-button:hover {
    background-color: #e93d1e;
    color: whitesmoke;
}