.custom-card {
    border-radius: 50% 0 30% 0;
    transition: box-shadow 0.3s ease;
    -webkit-border-radius: 50% 0 30% 0;
    -moz-border-radius: 50% 0 30% 0;
    -ms-border-radius: 50% 0 30% 0;
    -o-border-radius: 50% 0 30% 0;
}
  
  .custom-card:hover {
    box-shadow: 0 0 10px rgba(5, 153, 239, 0.5);
  }
  